import axios from 'axios';

axios.defaults.withCredentials = true;

const api = axios.create({
  baseURL: VITE_APP_URL,
});

api.interceptors.request.use(
  (config) => {
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

api.interceptors.response.use(
  (response) => response,
  (error) => {
    // if (error.response && error.response.status === 401) {
    //   window.location.href = '/login';
    // }
    return Promise.reject(error);
  }
);

export const authProvider = async (
  provider: string = 'github',
  originalUrl: string
) => {
  const response = await api.get(`/app/auth/login/${provider}`, {
    params: { original_url: originalUrl },
  });
  return response.data;
};

export const authRegisterProvider = async (
  provider: string = 'github',
  originalUrl: string
) => {
  const response = await api.get(`/app/auth/register/${provider}`, {
    params: { original_url: originalUrl },
  });
  return response.data;
};

export const callbackAuthProvider = async (
  params: string = '',
  provider: string = 'github'
) => {
  const response = await api.get(`/app/auth/callback/${provider}?${params}`);
  return response.data;
};

export const checkAuth = async () => {
  const response = await api.get('/app/authenticated');
  return response;
};

export const logout = async () => {
  const response = await api.get(`/app/auth/logout`);
  return response;
};

export const fetchApps = async () => {
  const response = await api.get('/apps');
  return response.data;
};

export const fetchApp = async (app: string) => {
  const response = await api.get(`/apps/${app}`);
  return response.data;
};

export const fetchDeployments = async (app: string) => {
  const response = await api.get(`/apps/${app}/deployments`);
  return response.data;
};

export const fetchReleases = async (app: string, deployment: string) => {
  const response = await api.get(
    `/apps/${app}/deployments/${deployment}/history`
  );
  return response.data;
};

export const fetchMetrics = async (app: string, deployment: string) => {
  const response = await api.get(
    `/apps/${app}/deployments/${deployment}/metrics`
  );
  return response.data;
};

export const patchRelease = async ({
  app,
  deployment,
  release,
}: {
  app: string;
  deployment: string;
  release: {
    appVersion: string;
    isMandatory: boolean;
    isDisabled: boolean;
    description: string;
    rollout?: number | null;
  };
}) => {
  const response = await api.patch(
    `/apps/${app}/deployments/${deployment}/release`,
    { packageInfo: release }
  );
  return response.data;
};

export const promoteRelease = async ({
  app,
  deployment,
  destDeploymentName,
}: {
  app: string;
  deployment: string;
  destDeploymentName: string;
}) => {
  const response = await api.post(
    `/apps/${app}/deployments/${deployment}/promote/${destDeploymentName}`
  );
  return response.data;
};

export const addNewApp = async (newApp: { name: string }) => {
  return api.post('/apps', newApp);
};

export const deleteApp = async (app: string) => {
  return api.delete(`/apps/${app}`);
};

export const editApp = async (updatedApp: {
  name: string;
  newName: string;
}) => {
  return api.patch(`/apps/${updatedApp.name}`, {
    name: updatedApp.newName,
  });
};

export const addNewAppDeployment = async ({
  app,
  deployment,
}: {
  app: string;
  deployment: { name: string };
}) => {
  return api.post(`/apps/${app}/deployments`, deployment);
};

export const editAppDeployment = async ({
  app,
  deployment,
}: {
  app: string;
  deployment: { name: string; newName: string };
}) => {
  return api.patch(`/apps/${app}/deployments/${deployment.name}`, {
    name: deployment.newName,
  });
};

export const deleteAppDeployment = async ({
  app,
  deployment,
}: {
  app: string;
  deployment: string;
}) => {
  return api.delete(`/apps/${app}/deployments/${deployment}`);
};

export const fetchAccount = async () => {
  const response = await api.get(`/account`);
  return response.data;
};

export const fetchAccessKeys = async () => {
  const response = await api.get(`/accessKeys`);
  return response.data;
};

export const addAccessKey = async (accessKey: {
  friendlyName: string;
  ttl: number;
}) => {
  return api.post(`/accessKeys`, accessKey);
};

export const revokeAccessKey = async (accessKey: string) => {
  return api.delete(`/accessKeys/${accessKey}`);
};
