import { lazy, Suspense } from 'react';

import { BrowserRouter, Routes, Route } from 'react-router';

import ProtectedRoute from './ProtectedRoute.tsx';
import Loader from './FullPageLoader.tsx';
import Login from '../screens/Login/Login.tsx';

const Main = lazy(() => import('../screens/Main/Main.tsx'));
const Applications = lazy(
  () => import('../screens/Applications/Applications.tsx')
);
const Application = lazy(
  () => import('../screens/Applications/application/Application.tsx')
);
const Settings = lazy(() => import('../screens/Settings/Settings.tsx'));
const Billing = lazy(() => import('../screens/Billing/Billing.tsx'));
const Documentation = lazy(
  () => import('../screens/Documentation/Documentation.tsx')
);

const Install = lazy(() => import('../screens/Documentation/Install.tsx'));

const Release = lazy(() => import('../screens/Documentation/Release.tsx'));

const Migration = lazy(() => import('../screens/Documentation/Migration.tsx'));

const Setup = lazy(() => import('../screens/Documentation/Setup.tsx'));

const Help = lazy(() => import('../screens/Help/Help.tsx'));
const GenerateCLIToken = lazy(
  () => import('../screens/Login/GenerateCLIToken.tsx')
);
const AuthCallback = lazy(() => import('../screens/Login/AuthCallback.tsx'));
const Register = lazy(() => import('../screens/Login/Register.tsx'));
const NotFound = lazy(() => import('../screens/404/NotFound.tsx'));

const RootRouter = () => {
  return (
    <BrowserRouter>
      <Suspense fallback={<Loader />}>
        <Routes>
          <Route path="/login" element={<Login />} />
          <Route path="/register" element={<Register />} />
          <Route path="/auth/callback/:provider" element={<AuthCallback />} />
          <Route
            index
            element={
              <ProtectedRoute>
                <Main />
              </ProtectedRoute>
            }
          />
          <Route
            path="cli-login"
            element={
              <ProtectedRoute>
                <GenerateCLIToken />
              </ProtectedRoute>
            }
          />
          <Route
            path="applications"
            element={
              <ProtectedRoute>
                <Applications />
              </ProtectedRoute>
            }
          />
          <Route
            path="applications/:application"
            element={
              <ProtectedRoute>
                <Application />
              </ProtectedRoute>
            }
          />
          <Route
            path="settings"
            element={
              <ProtectedRoute>
                <Settings />
              </ProtectedRoute>
            }
          />
          <Route
            path="billing"
            element={
              <ProtectedRoute>
                <Billing />
              </ProtectedRoute>
            }
          />
          <Route
            path="documentation"
            element={
              <ProtectedRoute>
                <Documentation />
              </ProtectedRoute>
            }
          />
          <Route
            path="documentation/install"
            element={
              <ProtectedRoute>
                <Install />
              </ProtectedRoute>
            }
          />
          <Route
            path="documentation/setup"
            element={
              <ProtectedRoute>
                <Setup />
              </ProtectedRoute>
            }
          />
          <Route
            path="documentation/migration"
            element={
              <ProtectedRoute>
                <Migration />
              </ProtectedRoute>
            }
          />
          <Route
            path="documentation/release"
            element={
              <ProtectedRoute>
                <Release />
              </ProtectedRoute>
            }
          />
          <Route
            path="help"
            element={
              <ProtectedRoute>
                <Help />
              </ProtectedRoute>
            }
          />
          <Route path="*" element={<NotFound />} />
        </Routes>
      </Suspense>
    </BrowserRouter>
  );
};

export default RootRouter;
