import { useSearchParams } from 'react-router';
import { useMemo } from 'react';

export const useOrigin = () => {
  const [searchParams] = useSearchParams();

  const originalUrl = useMemo(() => {
    return searchParams.get('original_url');
  }, [searchParams]);

  const originalUrlParam = useMemo(() => {
    return originalUrl ? `original_url=${encodeURIComponent(originalUrl)}` : '';
  }, [originalUrl]);

  const originalUrlEncoded = useMemo(() => {
    return originalUrl ? encodeURIComponent(originalUrl) : '/';
  }, [originalUrl]);

  return [originalUrlEncoded, originalUrlParam];
};
