import React from 'react';
import { Navigate, useLocation } from 'react-router';
import useAuthCheck from './useAuthCheck';
import Loader from './FullPageLoader.tsx';

interface ProtectedRouteProps {
  children: JSX.Element; // or ReactNode
}

const ProtectedRoute: React.FC<ProtectedRouteProps> = ({ children }) => {
  const { data, isLoading, error } = useAuthCheck();
  const location = useLocation();

  if (isLoading) {
    return <Loader />;
  }

  if (error || !data?.authenticated) {
    if (location.pathname !== '/') {
      // an empty string has pathname = "/"
      const originalUrl = encodeURIComponent(
        `${location.pathname}${location.search}`
      );
      return <Navigate to={`/login?original_url=${originalUrl}`} replace />;
    } else {
      return <Navigate to="/login" replace />;
    }
  }

  return children;
};

export default ProtectedRoute;
